import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Shield, Smartphone, Eye, EyeOff, Lock, User } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

const DashboardLoginPage = () => {
  const navigate = useNavigate();
  const { toast } = useToast();
  const [formData, setFormData] = useState({
    username: '',
    password: ''
  });
  const [showPassword, setShowPassword] = useState(false);
  const [loading, setLoading] = useState(false);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      // محاكاة عملية تسجيل الدخول
      if (formData.username === 'dashboard' && formData.password === 'dashboard123') {
        // حفظ حالة تسجيل الدخول
        localStorage.setItem('dashboardAuth', 'true');
        localStorage.setItem('dashboardUser', JSON.stringify({
          username: formData.username,
          loginTime: new Date().toISOString()
        }));

        toast({
          title: "مرحباً بك في لوحة التحكم!",
          description: "تم تسجيل الدخول بنجاح"
        });

        // التوجيه إلى لوحة التحكم
        navigate('/dashboard');
      } else {
        throw new Error('اسم المستخدم أو كلمة المرور غير صحيحة');
      }
    } catch (error: any) {
      console.error('Login error:', error);
      toast({
        title: "خطأ في تسجيل الدخول",
        description: error.message || "تحقق من اسم المستخدم وكلمة المرور",
        variant: "destructive"
      });
    } finally {
      setLoading(false);
    }
  };

  const handleQuickLogin = () => {
    setFormData({
      username: 'dashboard',
      password: 'dashboard123'
    });
  };

  return (
    <div className="min-h-screen bg-background flex items-center justify-center p-4">
      <Card className="w-full max-w-md shadow-lg">
        <CardHeader className="text-center">
          <div className="flex items-center justify-center mb-4">
            <span className="text-2xl font-extrabold tracking-wider uppercase text-primary" dir="ltr" style={{ fontFamily: 'Inter, sans-serif' }}>cashy link</span>
          </div>
          <CardTitle className="text-2xl font-bold text-gray-800">
            لوحة التحكم
          </CardTitle>
          <p className="text-muted-foreground mt-2">
            تسجيل الدخول للوصول إلى لوحة التحكم
          </p>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="text-sm font-medium text-gray-700 mb-2 block">
                اسم المستخدم
              </label>
              <div className="relative">
                <User className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                <Input
                  name="username"
                  type="text"
                  value={formData.username}
                  onChange={handleInputChange}
                  placeholder="أدخل اسم المستخدم"
                  className="pl-10"
                  required
                />
              </div>
            </div>

            <div>
              <label className="text-sm font-medium text-gray-700 mb-2 block">
                كلمة المرور
              </label>
              <div className="relative">
                <Lock className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                <Input
                  name="password"
                  type={showPassword ? "text" : "password"}
                  value={formData.password}
                  onChange={handleInputChange}
                  placeholder="أدخل كلمة المرور"
                  className="pl-10 pr-10"
                  autoComplete="off"
                  required
                />
                <button
                  type="button"
                  onClick={() => setShowPassword(!showPassword)}
                  className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600"
                >
                  {showPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                </button>
              </div>
            </div>

            <Button
              type="submit"
              disabled={loading}
              className="w-full bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700"
            >
              {loading ? "جاري تسجيل الدخول..." : "تسجيل الدخول"}
            </Button>
          </form>

          {/* Quick Login for Demo */}
          <div className="mt-6 pt-4 border-t border-gray-200">
            <div className="text-center">
              <p className="text-sm text-gray-600 mb-3">للتجربة السريعة:</p>
              <Button
                onClick={handleQuickLogin}
                variant="outline"
                className="w-full text-sm"
              >
                <Shield className="h-4 w-4 mr-2" />
                تسجيل دخول تجريبي
              </Button>
              <div className="mt-2 text-xs text-gray-500">
                اسم المستخدم: dashboard | كلمة المرور: dashboard123
              </div>
            </div>
          </div>

          {/* Security Notice */}
          <div className="mt-4 p-3 bg-blue-50 border border-blue-200 rounded-lg">
            <div className="flex items-start gap-2">
              <Shield className="h-4 w-4 text-blue-600 mt-0.5" />
              <div className="text-xs text-blue-700">
                <p className="font-medium mb-1">ملاحظة أمنية:</p>
                <p>تأكد من أنك تستخدم اتصال آمن وموثوق للوصول إلى لوحة التحكم.</p>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default DashboardLoginPage;